	module.exports = {"data":{"markdownRemark":{"html":"<p>Opened a plenary session and talked about the recent concerns about stability, including the decision-making level of the education system. It is mentioned that it is necessary to prevent some tendencies and raise some general issues to the principled ones. For example, if some students from the Mainland are having a difficult life, it is said to be caused by the differences between the economic development of the western region and the economic development of the coastal areas. This is a problem. Now some analysis always raises issues that do not have this nature, and finds social and political significance. This also becomes a kind of \"study.\" Such learning can cause unnecessary social problems under certain conditions.</p>\n<p>People who do \"learning\" often have an instinct that complicates simple problems and simplifies complex problems, thinking that this is \"study.\" In one case, this is true; under another condition, this is not true.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0042.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0042.md","frontmatter":{"title":"Friday, February 25th"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0042.md absPath of file >>> MarkdownRemark"}}

